import React, { Component } from "react";

export class Music extends Component {
    constructor(props) {
        super(props);

        this.url = "https://www.mboxdrive.com/Happy%20birthday%20Instrumental.mp3";
        this.audio = new Audio(this.url);
    }

    componentDidMount() {
        this.audio.play();
    }

    render() {
        return (
            <div></div>
        );
    }
}